<template>
    <div class="AccountSettingCompile">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="fiexoverTitle mt24">
        <div class="fiexover">
          <p>
            <i style="color: red; vertical-align: middle; display: inline-block;">*</i>代付公司名称
          </p>
          <div class="f34 cl_333" style="height: 100%; line-height: 1.02rem;">
            <input type="text" placeholder="请输入账号" class="bn text_right" v-model="companyName" @input="monitor">
<!--            <i class="iconfont op0"></i>-->
          </div>
        </div>
      </div>
      <div class="bg_fff mt24">
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8">
            <p>
              <i class="cl_red dib vm">*</i>代付协议
            </p>
          </div>
        </div>
        <div class="pl30 pt30 pb30">
          <span class="vm f24 cl_999 dis mb30">注:照片支持jpg/png/gif，文件大小不超过5M</span>
        <van-uploader :after-read="afterRead"  multiple :max-count="1"  :max-size="500 * 1024"  @oversize="onOversize" class="bg_fff vm uploading" preview-full-image  accept="image/*">
          <img src="../../../assets/img/common/basicsuploading.png" alt="" style="width: 100%">
        </van-uploader>
        <div class=" addUploader" v-if="fileList!=''">
          <img src="../../../assets/img/common/fileListDelete.png" alt="" class="fileListDelete" @click="fileListDelete">
          <img :src=fileList alt="" style="width: 1.44rem;height: 1.44rem">
        </div>
        </div>
      </div>
      <div class="mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick">提交</div>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "AccountSettingCompile",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle:{name:"编辑代付公司",left:true},
              //  文件上传
              fileList: "",
              //公司名称
              companyName:"",
              SubmitClassbox:false,
              //传递过来的index
              transmitIndex:""

            }
        },
        methods: {
          //时刻监听
          monitor(){
            if(this.fileList==""||this.companyName==""){
              console.log(this.fileList,this.companyName);
              this.SubmitClassbox=false
            }else{
              console.log(this.fileList,this.companyName);
              this.SubmitClassbox=true
            }

          },
          //提交跳转
          SubmitClassClick(){
            this.$router.push({
              name:"AccountSetting",
              params:{name:this.companyName,index:this.transmitIndex}
            })
          },
          //删除文件上传
          fileListDelete(){
            this.fileList="";
            this.monitor();
          },
          //文件上传
          afterRead(file) {
            // 此时可以自行将文件上传至服务器
            this.fileList=file.content;
            this.monitor();
          },
          onOversize(file) {
            console.log(file);
            // console.log('文件大小不能超过 500kb');
          },
        },
        created() {
          this.transmitIndex=this.$route.params.index;
        }
    }

</script>

<style scoped>
  .fileListDelete{position: absolute;right: -.2rem;top: -.2rem;width: .4rem;height: .4rem}
  .uploading{width: 1.44rem;height: 1.44rem;vertical-align: middle;margin-right: .2rem}
  .uploading .van-uploader__wrapper{width: 100%;height: 100%}
  .uploading .van-uploader__upload{margin: 0;height: 100%}
  .addUploader{
    display: inline-block;vertical-align: middle;position: relative;width: 1.44rem;height: 1.44rem;
  }
</style>



